/*
 * Description: Component for editing Admin users
 */

import React, { Component } from 'react';
import {
  Alert,
  Container,
  Button,
  Form,
  FormGroup,
  Input,
  Col,
  FormFeedback,
} from 'reactstrap';
import axios from 'axios';

class EditAdmin extends Component {
  constructor(props) {
    super(props);
    this.state = {
      id: '',
      admin_name: '',
      password: '',
      confirmPW: '',
      validate: false,
      invalidAdmin: false,
      visible: false,
      errorMsg: '',
    };
  }

  componentDidMount() {
    this.getAdmin();
  }

  // On form input change handler to set state
  onChange = e => {
    this.setState({
      [e.target.name]: e.target.value,
      invalidAdmin: false,
      validate: false,
      invalidPW: false,
    });
  };

  // Get Admin that will be edited
  getAdmin = () => {
    this.setState({
      id: this.props.location.state.id,
      admin_name: this.props.location.state.admin_name,
    });
  };

  // Save update to admin
  saveEdit = e => {
    let token = localStorage.getItem('access_token');
    let config = {
      headers: { Authorization: `Bearer ${token}` },
    };

    e.preventDefault();
    const { password, confirmPW, id, admin_name } = this.state;
    // PW validation
    if (admin_name !== '' && password !== confirmPW) {
      this.setState({ validate: true });
    } else if (admin_name === '') {
      this.setState({
        invalidAdmin: true,
        errorMsg: 'You must enter a username.',
      });
    } else if (password === '') {
      this.setState({ invalidPW: true });
    } else {
      // Patch request to API endpoint to update admin.
      // Passes access token for auth.
      axios
        .patch(
          `/admin/${id}`,
          {
            admin_name: this.state.admin_name,
            password: this.state.password,
          },
          config
        )
        .then(res => {
          console.log(res);
          this.setState(
            {
              admin_name: '',
              password: '',
              confirmPW: '',
              validate: false,
              invalidAdmin: false,
              visible: true,
            },
            () => {
              // Redirect back to admin dashboard after 1 seconds on success
              window.setTimeout(() => {
                this.props.history.push({
                  pathname: '/adminDash',
                  state: {
                    from: 2,
                  },
                });
              }, 1000);
            }
          );
        })
        .catch(err => {
          console.log(err);
          this.setState({
            invalidAdmin: true,
            errorMsg: 'This username is unvailable.',
          });
        });
    }
  };

  render() {
    return (
      <div>
        <Container>
          <Alert color="success" isOpen={this.state.visible} className="form--alert">
            Admin has been saved!
          </Alert>
          <div className="login--form">
            <Col sm="12" md={{ size: 6, offset: 3 }}>
              <h1 className="login--title">Edit Admin</h1>
            </Col>
            <Form onSubmit={this.saveEdit}>
              <Col sm="12" md={{ size: 6, offset: 3 }}>
                <FormGroup>
                  <Input
                    className="input--form"
                    type="text"
                    invalid={this.state.invalidAdmin}
                    name="admin_name"
                    id="admin_id"
                    value={this.state.admin_name}
                    placeholder="Admin Name"
                    onChange={this.onChange}
                  />
                  <FormFeedback invalid="true">
                    {this.state.errorMsg}
                  </FormFeedback>
                </FormGroup>
              </Col>

              <Col sm="12" md={{ size: 6, offset: 3 }}>
                <FormGroup>
                  <Input
                    className="input--form"
                    invalid={this.state.invalidPW}
                    type="password"
                    name="password"
                    id="pw_id"
                    value={this.state.password}
                    placeholder="New Password"
                    onChange={this.onChange}
                  />
                  <FormFeedback invalid="true">
                    You must enter a password.
                </FormFeedback>
                </FormGroup>
              </Col>

              <Col sm="12" md={{ size: 6, offset: 3 }}>
                <FormGroup>
                  <Input
                    className="input--form"
                    invalid={this.state.validate}
                    type="password"
                    name="confirmPW"
                    id="cpw_id"
                    value={this.state.confirmPW}
                    placeholder="Confirm Password"
                    onChange={this.onChange}
                  />
                  <FormFeedback invalid="true">
                    Password doesn't match!
                </FormFeedback>
                </FormGroup>
              </Col>

              <Col sm="12" md={{ size: 6, offset: 3 }}>
                <Button color="primary" type="submit" className="input--form">Save Admin</Button>
              </Col>
            </Form>
          </div>
        </Container>
      </div>
    );
  }
}

export default EditAdmin;
